                                    </td>
                                    <td class="table-withdrawals__cell">
                                        <a v-if="withdrawal.invoice_uri" :href="withdrawal.invoice_uri" target="_blank"
                                            class="invoice-link">Download</a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div v-else class="withdrawals">
                        <div class="withdrawal" v-for="withdrawal in withdrawals" :key="withdrawal.id">
                            <span class="withdrawal__heading">ID</span>
                            <span>#{{ withdrawal.id }}</span>
                            <span class="withdrawal__heading">Amount</span>
                            <span>€{{ formatCurrency(withdrawal.amount_eur) }}</span>
                            <span class="withdrawal__heading">Status</span>
                            <span>
                                <div>{{ withdrawal.status_translated }}</div>
                                <div v-if="withdrawal.status_text" class="withdrawal__status-text">
