            <!-- Show agreement form or details only if device is linked -->
            <div v-if="(!hasSignedAgreement || hasTerminatedAgreement) && isDeviceLinked && !isWithdrawalsView && !isDevicesView"
                class="section form-section">
                <h2 class="form-title">To enable automatic reimbursement please fill in this form and click
                    "continue".
                    Submitting of this form creates legal binding between you and appdb.</h2>

                <form @submit.prevent="submitForm">
                    <div class="form-group">
                        <label for="name">Name, Surname</label>
                        <input type="text" id="name" v-model="formData.name" required class="form-control">
