                    <div class="form-row">
                        <div class="form-group">
                            <label for="index">Postal/Zip Code</label>
                            <input type="text" id="index" v-model="formData.index" required class="form-control">
                        </div>

                        <div class="form-group">
                            <label for="country">Country</label>
                            <select id="country" v-model="formData.country" required class="form-control">
                                <option value="" disabled>Select a country</option>
                                <option v-for="country in countries" :key="country" :value="country">{{ country }}
                                </option>
                            </select>
                        </div>
                    </div>

                    <div class="form-group">
                        <label for="bank_details">Payout Details</label>
                        <p class="bank-info">We support payouts via PayPal, wise.com or SWIFT bank transfer. Account
                            should be opened in your name, we can not transfer funds to third party accounts.</p>
                        <textarea id="bank_details" v-model="formData.bank_details" required class="form-control"
                            rows="4"></textarea>
                    </div>

                    <div class="form-actions">
                        <button type="submit" class="btn-primary">Continue</button>
                    </div>
                </form>
            </div>

            <!-- Show device linking prompt if trying to access protected views without device -->
            <div v-else-if="(isDevicesView || isWithdrawalsView) && !isDeviceLinked" class="section device-linking">
                <h2 class="form-title">To view this page, you need to link your device first</h2>
                <router-link to="/link" class="btn-primary">Link Your Device</router-link>
            </div>
        </template>
    </section>
</template>

<script>
import Vue from 'vue';
import Loader from '@/components/Loader';
import VMarkdown from '@/components/VMarkdown.vue';
import ImportantMessage from '@/components/ImportantMessage.vue';

export default {
    components: { Loader, VMarkdown, ImportantMessage },
    data() {
        return {
            isLoading: true,
            contract: '',
            submitting: false,
            showAgreementText: false,
            countries: [
                'Afghanistan', 'Albania', 'Algeria', 'Andorra', 'Angola', 'Antigua and Barbuda', 'Argentina', 'Armenia', 'Australia', 'Austria',
                'Azerbaijan', 'Bahamas', 'Bahrain', 'Bangladesh', 'Barbados', 'Belarus', 'Belgium', 'Belize', 'Benin', 'Bhutan',
                'Bolivia', 'Bosnia and Herzegovina', 'Botswana', 'Brazil', 'Brunei', 'Bulgaria', 'Burkina Faso', 'Burundi', 'Cabo Verde', 'Cambodia',
                'Cameroon', 'Canada', 'Central African Republic', 'Chad', 'Chile', 'China', 'Colombia', 'Comoros', 'Congo', 'Costa Rica',
                'Croatia', 'Cuba', 'Cyprus', 'Czechia', 'Denmark', 'Djibouti', 'Dominica', 'Dominican Republic', 'East Timor', 'Ecuador',
                'Egypt', 'El Salvador', 'Equatorial Guinea', 'Eritrea', 'Estonia', 'Eswatini', 'Ethiopia', 'Fiji', 'Finland', 'France',
                'Gabon', 'Gambia', 'Georgia', 'Germany', 'Ghana', 'Greece', 'Grenada', 'Guatemala', 'Guinea', 'Guinea-Bissau',
                'Guyana', 'Haiti', 'Honduras', 'Hungary', 'Iceland', 'India', 'Indonesia', 'Iran', 'Iraq', 'Ireland',
                'Israel', 'Italy', 'Jamaica', 'Japan', 'Jordan', 'Kazakhstan', 'Kenya', 'Kiribati', 'Korea, North', 'Korea, South',
                'Kosovo', 'Kuwait', 'Kyrgyzstan', 'Laos', 'Latvia', 'Lebanon', 'Lesotho', 'Liberia', 'Libya', 'Liechtenstein',
                'Lithuania', 'Luxembourg', 'Madagascar', 'Malawi', 'Malaysia', 'Maldives', 'Mali', 'Malta', 'Marshall Islands', 'Mauritania',
                'Mauritius', 'Mexico', 'Micronesia', 'Moldova', 'Monaco', 'Mongolia', 'Montenegro', 'Morocco', 'Mozambique', 'Myanmar',
                'Namibia', 'Nauru', 'Nepal', 'Netherlands', 'New Zealand', 'Nicaragua', 'Niger', 'Nigeria', 'North Macedonia', 'Norway',
                'Oman', 'Pakistan', 'Palau', 'Panama', 'Papua New Guinea', 'Paraguay', 'Peru', 'Philippines', 'Poland', 'Portugal',
                'Qatar', 'Romania', 'Russia', 'Rwanda', 'Saint Kitts and Nevis', 'Saint Lucia', 'Saint Vincent and the Grenadines', 'Samoa', 'San Marino', 'Sao Tome and Principe',
                'Saudi Arabia', 'Senegal', 'Serbia', 'Seychelles', 'Sierra Leone', 'Singapore', 'Slovakia', 'Slovenia', 'Solomon Islands', 'Somalia',
                'South Africa', 'South Sudan', 'Spain', 'Sri Lanka', 'Sudan', 'Suriname', 'Sweden', 'Switzerland', 'Syria', 'Taiwan',
                'Tajikistan', 'Tanzania', 'Thailand', 'Togo', 'Tonga', 'Trinidad and Tobago', 'Tunisia', 'Turkey', 'Turkmenistan', 'Tuvalu',
                'Uganda', 'Ukraine', 'United Arab Emirates', 'United Kingdom', 'United States', 'Uruguay', 'Uzbekistan', 'Vanuatu', 'Vatican City', 'Venezuela',
                'Vietnam', 'Yemen', 'Zambia', 'Zimbabwe'
            ],
            formData: {
                name: '',
                address: '',
                country: '',
                index: '',
                city: '',
                state: '',
                bank_details: ''
            },
            devices: [],
            loadingDevices: false,
            withdrawals: [],
            loadingWithdrawals: false,
            isWithdrawalProcessing: false
        };
    },
    computed: {
        title() {
            return 'Auto Reimbursement';
        },
        metaInfo() {
            return {
                title: 'Auto Reimbursement',
                meta: [
                    {
                        name: 'description',
                        content: 'Auto Reimbursement information',
                    },
                    {
                        name: 'keywords',
                        content: 'auto, reimbursement',
                    },
                ],
            };
        },
        hasSignedAgreement() {
            return this.$store.getters.config?.plus_account?.automatic_reimbursement_signed_at;
        },
        hasTerminatedAgreement() {
            return this.$store.getters.config?.plus_account?.automatic_reimbursement_terminated_at;
        },
        agreementDetails() {
            const plusAccount = this.$store.getters.config?.plus_account || {};
            return {
                name: plusAccount.automatic_reimbursement_name || '',
                address: plusAccount.automatic_reimbursement_address || '',
                country: plusAccount.automatic_reimbursement_country || '',
                index: plusAccount.automatic_reimbursement_index || '',
                city: plusAccount.automatic_reimbursement_city || '',
                state: plusAccount.automatic_reimbursement_state || '',
                bank_details: plusAccount.automatic_reimbursement_bank_details || '',
                signed_at: plusAccount.automatic_reimbursement_signed_at || 0,
                terminated_at: plusAccount.automatic_reimbursement_terminated_at || 0
            };
        },
        deviceResetAt() {
            return this.$store.getters.config?.plus_account?.device_reset_at || null;
        },
        formatDeviceResetDate() {
            if (!this.deviceResetAt) return '';
            return new Date(this.deviceResetAt * 1000).toLocaleDateString();
        },
        formattedSignedDate() {
            if (!this.agreementDetails.signed_at) return '';
            const date = new Date(this.agreementDetails.signed_at * 1000);
            return date.toLocaleString();
        },
        formattedTerminatedDate() {
            if (!this.agreementDetails.terminated_at) return '';
            const date = new Date(this.agreementDetails.terminated_at * 1000);
            return date.toLocaleString();
        },
        deviceCount() {
            return this.$store.getters.config?.plus_account?.automatic_reimbursement_devices_amount || 0;
        },
        isDevicesView() {
            return this.$route.path.endsWith('/devices');
        },
        isMobile() {
            return window.innerWidth < 768;
        },
        financialDetails() {
            const plusAccount = this.$store.getters.config?.plus_account || {};
            return {
                balance: this.formatCurrency(plusAccount.automatic_reimbursement_balance_eur),
                availableForWithdrawal: this.formatCurrency(plusAccount.automatic_reimbursement_available_for_withdrawal_eur),
                withdrawalPending: this.formatCurrency(plusAccount.automatic_reimbursement_withdrawal_pending_eur),
                withdrawalCompleted: this.formatCurrency(plusAccount.automatic_reimbursement_withdrawal_completed_eur)
            };
        },
        availableSlots() {
            const plusAccount = this.$store.getters.config?.plus_account || {};
            const usedSlots = parseInt(plusAccount.used_slots) || 0;
            var to_return = 20 - usedSlots;
            if (to_return < 0) {
                to_return = 0;
            }
            return to_return;
        },
        isWithdrawalsView() {
            return this.$route.path.endsWith('/withdrawals');
        },
        isDeviceLinked() {
            return this.$store.getters.isDeviceLinked;
        }
    },
    watch: {
        isDevicesView(newVal) {
            if (newVal && this.devices.length === 0 && this.isDeviceLinked) {
                this.loadDevices();
            }
        },
        isWithdrawalsView(newVal) {
            if (newVal && this.withdrawals.length === 0 && this.isDeviceLinked) {
                this.loadWithdrawals();
            }
        }
    },
    created() {
        this.isLoading = true;

        this.$api.post('get_defaults', { 'for': 'auto_reimbursement_md' }, false)
            .then((response) => {
                if (response.success && response.data) {
                    this.contract = response.data.contract || '';
                } else {
                    console.error('Failed to fetch markdown content:', response);
                }
            })
            .catch((error) => {
                console.error('Error fetching markdown content:', error);
            })
            .finally(() => {
                this.isLoading = false;

                // Load appropriate data based on the current route and device linkage
                if (this.isDeviceLinked) {
                    if (this.isDevicesView) {
                        this.loadDevices();
                    } else if (this.isWithdrawalsView) {
                        this.loadWithdrawals();
                    }
                }
            });
    },
    methods: {
        formatCurrency(value) {
            const num = parseFloat(value) || 0;
            return num.toFixed(2);
        },
        toggleAgreementText() {
            this.showAgreementText = !this.showAgreementText;
        },
        submitForm() {
            if (this.submitting) return;

            this.submitting = true;

            // When handleErrors is true (the 3rd parameter), the API service will:
            // 1. Show error notifications automatically
            // 2. Throw an error when result.success is false
            // 3. Only return result.data on success (not the full response with success property)
            this.$api.post('edit_plus_dev_account_reimbursement_settings', {
                name: this.formData.name,
                address: this.formData.address,
                country: this.formData.country,
                index: this.formData.index,
                city: this.formData.city,
                state: this.formData.state,
                bank_details: this.formData.bank_details
            }, true) // Setting handleErrors to true
                .then(() => { // We get data directly, not a response object with success property
                    // Update device information using the correct store actions
                    // First get the configuration which includes plus_account details
                    return this.$store.dispatch('getConfig')
                        .then(() => {
                            // Then get updated devices information
                            return this.$store.dispatch('getDevices');
                        })
                        .then(() => {
                            Vue.notify({
                                type: 'success',
                                text: 'Agreement has been successfully signed'
                            });
                        });
                })
                .catch((error) => {
                    // The API service already showed an error notification
                    console.error('Error submitting form:', error);
                })
                .finally(() => {
                    this.submitting = false;
                });
        },
        loadDevices() {
            this.loadingDevices = true;

            this.$api.getP2PPurchases({ type: 'premium' })
                .then((response) => {
                    this.devices = response.data || [];
                })
                .catch((error) => {
                    console.error('Error loading devices:', error);
                    Vue.notify({
                        type: 'error',
                        text: 'Failed to load registered devices'
                    });
                })
                .finally(() => {
                    this.loadingDevices = false;
                });
        },
        loadWithdrawals() {
            this.loadingWithdrawals = true;

            this.$api.getWithdrawals()
                .then((response) => {
                    this.withdrawals = response.data || [];
                })
                .catch((error) => {
                    console.error('Error loading withdrawals:', error);
                    Vue.notify({
                        type: 'error',
                        text: 'Failed to load withdrawal history'
                    });
                })
                .finally(() => {
                    this.loadingWithdrawals = false;
                });
        },
        requestWithdrawal() {
            if (this.isWithdrawalProcessing) return;

            this.isWithdrawalProcessing = true;

            const amount = this.financialDetails.availableForWithdrawal;
            const confirmMessage = `Are you sure to withdraw €${amount} to your configured payment method?`;

            this.$confirm(confirmMessage, { yesOrNo: true })
                .then(() => {
                    return this.$api.post('request_withdrawal', {}, true);
                })
                .then(() => {
                    // Refresh configuration data to update financial details
                    return this.$store.dispatch('getConfig');
                })
                .then(() => {
                    // Update device information
                    return this.$store.dispatch('getDevices');
                })
                .then(() => {
                    // On success and after refreshing data, redirect to withdrawals page
                    this.$router.push('/my/auto-reimbursement/withdrawals');
                })
                .catch((error) => {
                    // If it's a real error (not just cancellation of dialog), log it
                    if (error) {
                        console.error('Withdrawal request failed:', error);
                    }
                })
                .finally(() => {
                    this.isWithdrawalProcessing = false;
                });
        }
    }
};
</script>

